@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,h2,h3,p{margin:0 0 0.4rem}a{margin:0}body{margin:0;font-family:'Spartan', sans-serif;font-size:15px}body .B-Header{background:url(/static/media/bg-header-mobile.6b5ff5a0.svg),no-repeat,linear-gradient(#5ba4a4, #5ba4a4);height:140px;background-size:cover}body .B-Header .Container{height:140px;position:relative}body .B-Header .Container .Grid{min-height:100%;padding:0 1rem}body .B-Header__Title{color:white;margin:0}body .Container{margin:0 auto;max-width:1100px}body .Container .Grid{position:relative;display:flex;flex-wrap:wrap;padding:1rem}body .Container .Align-Center{align-items:center}body .B-Main{min-height:100vh;background:#effafa}body .B-Loading{font-weight:500}body .B-Listing{background-color:white;width:100%;padding:1rem;margin:2rem 0;border-radius:0.5rem;box-shadow:0px 0.5rem 1rem 0px rgba(94,163,164,0.2);border-left:0.5rem solid transparent}body .B-Listing--New{border-left:0.5rem solid #5ba4a4}body .B-Listing__Logo{position:absolute;width:4rem;transform:translateY(-5rem)}body .B-Listing__Info{margin:2rem 0 0}body .B-Listing__Heading{display:flex;align-items:center}body .B-Listing__Company{color:#2c3a3a;font-weight:500;margin:0}body .B-Listing__New{border-radius:2rem;color:white;background-color:#2c3a3a;padding:0.4rem 0.8rem;margin:0 0.5rem;font-weight:700}body .B-Listing__Featured{border-radius:2rem;color:white;background-color:#2c3a3a;padding:0.4rem 0.8rem;font-weight:700;white-space:nowrap}body .B-Listing__Link{text-decoration:none}body .B-Listing__Title{color:#5ba4a4;font-weight:700;margin:1rem 0}body .B-Listing__Title:hover{color:#7b8e8e}body .B-Listing__Bullets{color:#7b8e8e}body .B-Listing__Bullets::after{content:"\00B7";font-size:20px;font-weight:700;padding:0 0.2rem}body .B-Listing__Bullets:last-child:after{content:""}body .B-Keywords{display:flex;align-items:center;flex-wrap:wrap}body .B-Keywords__Tag{display:block;color:#5ba4a4;font-weight:700;background:#effafa;border-radius:0.4rem;padding:0.4rem 0.8rem;margin:0 1rem 1rem 0}body hr{border-top:1px solid #7b8e8e;border-right:none;border-bottom:none;border-left:none;margin:1rem 0}@media (min-width: 768px){body .App-header{background:url(/static/media/bg-header-desktop.6e2f7196.svg),no-repeat,linear-gradient(#5ba4a4, #5ba4a4)}body .B-Listing{display:flex;align-items:center;justify-content:space-between;margin:1rem 0}body .B-Listing__Logo{position:relative;height:100px;width:100px;transform:translateY(0);margin-right:2rem}body .B-Listing__Details{display:flex;align-items:center;width:70%}body .B-Listing__Info{margin:0 2rem 0 0}body .B-Keywords{justify-content:flex-end}body .B-Keywords__Tag{margin:0.5rem}body hr{display:none}}

